<template>
  <div class="order-box">
    <s-header :name="'order in advance history order'" :back="'/user'"></s-header>
    <van-tabs
      @change="onChangeTab"
      :color="'#1baeae'"
      :title-active-color="'#1baeae'"
      class="order-tab"
      v-model="status"
    >
      <van-tab  title="Orders in the reservation" name="0"></van-tab>
      <van-tab  title="Historical order" name="1"></van-tab>
    </van-tabs>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" class="order-list-refresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        @offset="300"
      >
        <div
          v-for="(item, index) in list"
          :key="index"
          class="order-item-box"
        >
          <!-- @click="goTo(item.orderNo)" -->
          <div class="order-item-header">
            <span>Buy back:</span>
            <span>
              Order No.
              <span>2202556545652685565</span>
            </span>
          </div>
            <!-- v-for="one in item.detail" -->
            <!-- :key="one" -->
          <van-card
            :title="item.detail.goodsName"
            :thumb="prefix(item.detail.goodsCoverImg)"
          >
            <div slot="desc" class="quan">
              <span class="item">10gm</span>
              <span class="item">Quantity:{{item.ops}}</span>
            </div>
            <div slot="price" class="item-price">
              <div class="price-buy">
                ₹546556.33/
                <span>₹4640.940</span>
              </div>
              <div class="price-now">
                Current different value:
                <span>640.940</span>
              </div>
              <div class="price-now">
                Canceled time:
                <span>{{item.createTime}}</span>
              </div>
            </div>
          </van-card>
          <div class="buy-again">
            <div class="again-left">
              <span>Pre-payment₹1500</span>
              <span>(including serve fee)</span>
            </div>
            <div class="again-right" v-if="billStatus==0">
              <span :data-status="item.billId"  @click="btnChangeStutas(item.billId,item.billStatus,index)">End order</span>
            </div>
               <div class="again-right" v-else>
              <span :data-status="item.billId"  @click="btnChangeStutas(item.billId,item.billStatus)">Buy again</span>
            </div>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import sHeader from "@/components/SimpleHeader";
import { getOrderList } from "../service/order";
import { prefix } from "@/common/js/utils";
import { GetMybills,UpdateBill } from "../service/goldorder";
import {getDetail} from '../service/good'
import { Toast } from 'vant';

export default {
  data() {
    return {
      status: "",
      billStatus:0,
      loading: false,
      finished: false,
      refreshing: false,
      list: [],
      page: 1
    };
  },
  computed: {
    // shou() {
    //   return `10gm  Quanity:5`;
    // }
  },
  components: {
    sHeader
  },
  async mounted() {
    // this.loadData()
    // this.getMybills();
  },
  methods: {
     delete(id){
                 //根据id找出索引，
　　　　　　　　　//如果找到索引直接调用数组splice方法
                  let flag = false;
                    this.list.some((item,i) => {
                        if(i==id){  
                          flag = true;     
                        this.list.splice(i,1);
                        return true;             
                      }
                    })
                    return flag
     },
    btnChangeStutas(billId,billStatus,index){
      console.log("index",index);
    //  console.log(billId,billStatus);
      if(billStatus==0){
     UpdateBill({
       billId:billId,
       billStatus:1,
       unwindingPoint:"3200"
        }).then(res=>{
      //  console.log(res);
     
      let flag =  this.delete(index);
      if(flag){
 Toast.success("Cancel Success");
      }else{
        Toast.fail("Cancel Fail");
      }
      

        })
      
      }
    },
    btnclick(id){
      this.billStatus = id;
      this.getMybills();
    },
    async getMybills() {
      const res = await GetMybills();
      // console.log(res.data);
      let lists = res.data;
      let list = [];
      let ls = [];
      lists.forEach((element) => {
        // console.log("index",element);
        if(element.billStatus ==this.billStatus){
          ls.push(element);
        } 
      });
      ls.forEach((item,index) =>{

       getDetail(item.goodId).then(res=>{
          // console.log("index",res);
          item.detail=res.data;
          list.push(item);
          // console.log(lists.length-1,index);
          if(ls.length-1==index){
            list.reverse();
            this.list = list;
          }

        }).catch(err=>{
          // console.log("element",index,err);
        })
      })

       this.loading = false;
      this.finished = true;
      
      console.log("list",this.list);

    },

    async loadData() {
      const {
        data,
        data: { list }
      } = await getOrderList({ pageNumber: this.page, status: this.status });
      this.list = this.list.concat(list);
      this.totalPage = data.totalPage;
      this.loading = false;
      if (this.page >= data.totalPage) this.finished = true;
    },
    onChangeTab(name, title) {
      this.billStatus = name;
      //  this.getMybills();
      this.onRefresh();
    },
    goTo(id) {
      this.$router.push({ path: `order-detail?id=${id}` });
    },
    goBack() {
      this.$router.go(-1);
    },
    onLoad() {
      // if (!this.refreshing && this.page < this.totalPage) {
      //   this.page = this.page + 1;
      // }
      if (this.refreshing) {
        this.list = [];
        this.refreshing = false;
      }
      this.getMybills();
      // this.loadData();
    },
    onRefresh() {
      this.refreshing = true;
      this.finished = false;
      this.loading = true;
      this.page = 1;
      this.onLoad();
      // this.getMybills();
    }
  }
};
</script>

<style lang="less" scoped>
@import "../common/style/mixin";
.order-box {
  .order-header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    .fj();
    .wh(100%, 44px);
    line-height: 44px;
    padding: 0 10px;
    .boxSizing();
    color: #252525;
    background: #fff;
    border-bottom: 1px solid #dcdcdc;
    .order-name {
      font-size: 14px;
    }
  }
  .order-tab {
    margin-top: 44px;
    position: fixed;
    left: 0;
    z-index: 1000;
    width: 100%;
  }
  .order-list-refresh {
    margin-top: 68px;
    .van-card__content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      .quan {
        display: flex;
        justify-content: space-between;
        padding-right: 10%;
        padding-bottom: 10px;
        padding-top: 10px;
      }
      .item-price {
        display: flex;
        flex-direction: column;
        color: #666;
        .price-buy {
          span {
            color: #659759;
          }
        }
        .price-now {
          span {
            color: #659759;
          }
        }
      }
      .van-card__title {
        font-weight: 600;
        font-size: 14px;
      }
    }
    .van-pull-refresh__head {
      background: #f9f9f9;
    }
    .van-list {
      min-height: calc(100vh - 88px);
      background: #f9f9f9;
      margin-top: 20px;
    }
    .order-item-box {
      margin: 20px 10px;
      background-color: #fff;
      .order-item-header {
        padding: 10px 10px 10px 10px;
        display: flex;
        font-weight: 600;
        border-bottom: 0.5px solid #dddddd;
        justify-content: space-between;
        span {
          span {
            font-weight: 400;
          }
        }
      }
      .van-card {
        background-color: #fff;
        margin-top: 0;
      }
      .buy-again {
        line-height: 24px;
        border-top: 0.5px solid #dddddd;
        display: flex;
        .again-left {
          display: flex;
          flex: 3;
          flex-direction: column;
          span:nth-child(2) {
            color: #8a8a8a;
          }
        }
        .again-right {
          flex: 1;
          span {
            display: inline-block;
            line-height: 30px;
            padding: 0 10px;
            margin: 9px 0;
            border-radius: 10px;
            background-color: #f2f2f2;
            font-weight: 600;
          }
        }
      }
    }
  }
}
</style>
