import axios from '../utils/axios'
import {baseApi} from '../utils/api'
// 获取我的账单列表
export function GetMybills() {
  return axios.get(`/bill-api/v1/GetMybills`); //${baseApi}
}

//修改账单状态
export function   UpdateBill(params) {
  return axios.post(`/bill-api/v1/update`,params); //${baseApi}
}